.profile {
  min-height: calc(100vh - 57px);
  padding: 30px 0;
  border: 10px solid #ffd78e;
  box-sizing: border-box;
  background-color: #fff;
}
.inner {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}
h3 {
  margin: 30px 0 50px;
}
h3 i {
  display: block;
  font-size: 2.5em;
  font-weight: 200;
}
.description {
  border-left: 3px solid #ffd78e;
  padding: 10px 20px;
  box-sizing: border-box;
  line-height: 1.5;
}
.description p b {
  font-size: 2em;
}
.description ul {
  margin-top: 20px;
}
.description li {
  position: relative;
  margin-top: 5px;
  padding-left: 15px;
  font-size: 1.2em;
  font-weight: 300;
}
.description li:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0.5em;
  width: 4px;
  height: 4px;
  background-color: #ffd78e;
  border-radius: 50%;
}
.link {
  display: flex;
  width: 500px;
  max-width: 100%;
  margin: 40px 0;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.link a {
  color: #333;
}
.link a:hover {
  color: #930909;
}
.list {
  padding-top: 30px;
}
.list h4 {
  margin: 30px 0;
  font-size: 2.2em;
  font-weight: 600;
  color: #930909;
}
.list li {
  font-size: 1em;
  font-weight: 300;
  line-height: 1.6;
}
.list li b {
  font-weight: 500;
}
.list li + li {
  margin-top: 20px;
}
.list li span {
  display: inline-block;
  padding: 3px 10px;
  margin: 2px;
  background-color: #ffd78e;
  border-radius: 5px;
  font-size: 0.9em;
}
